"use client"

import type React from "react"


import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Mail, Phone, MapPin, Send, MessageCircle, Sparkles, X, Zap, Loader2 } from "lucide-react"
import { toast } from "sonner"
import { submitContactMessage } from "@/lib/actions/contact"

interface ChatMessage {
  id: string
  text: string
  sender: "user" | "bot"
  timestamp: Date
}

export default function ContactPage() {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    phone: "",
    subject: "",
    message: "",
  })
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [submitted, setSubmitted] = useState(false)

  const [chatOpen, setChatOpen] = useState(false)
  const [chatMessages, setChatMessages] = useState<ChatMessage[]>([
    {
      id: "1",
      text: "Hi! Welcome to MelodyMaker Studio. I'm your AI assistant. How can I help you today? Feel free to ask about our services, pricing, collaboration opportunities, or anything else!",
      sender: "bot",
      timestamp: new Date(),
    },
  ])
  const [chatInput, setChatInput] = useState("")

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }))
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsSubmitting(true)

    try {
      const result = await submitContactMessage(formData)

      if (result.success) {
        setSubmitted(true)
        toast.success("Message sent successfully!")
        setFormData({ name: "", email: "", phone: "", subject: "", message: "" })

        setTimeout(() => {
          setSubmitted(false)
        }, 5000)
      } else {
        toast.error(result.error || "Failed to send message.")
      }
    } catch (error) {
      toast.error("An unexpected error occurred.")
      console.error(error)
    } finally {
      setIsSubmitting(false)
    }
  }

  const handleChatSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    if (!chatInput.trim()) return

    const userMessage: ChatMessage = {
      id: Date.now().toString(),
      text: chatInput,
      sender: "user",
      timestamp: new Date(),
    }

    setChatMessages((prev) => [...prev, userMessage])
    setChatInput("")

    // Simulate AI response
    setTimeout(() => {
      const botResponses = [
        "That's a great question! Our team specializes in providing AI-powered music production solutions. Would you like to know more about our specific services?",
        "I'd be happy to help! We offer custom songs, collaborations with our featured artists, and full label services. Which interests you most?",
        "Excellent! Our personalized songs start at $499 and include professional mixing and mastering. Would you like to explore more details?",
        "Our team is available for consultations! You can reach us via email at hello@melodymaker.com or use the contact form above.",
        "Thanks for your interest! Let me connect you with our team for a personalized consultation. Is there anything else I can help with?",
      ]

      const botResponse: ChatMessage = {
        id: (Date.now() + 1).toString(),
        text: botResponses[Math.floor(Math.random() * botResponses.length)],
        sender: "bot",
        timestamp: new Date(),
      }

      setChatMessages((prev) => [...prev, botResponse])
    }, 800)
  }

  return (
    <main className="pt-16">
      {/* Hero Section */}
      <section className="min-h-[60vh] bg-gradient-to-b from-background to-background/50 flex items-center relative overflow-hidden">
        <div className="absolute inset-0">
          <div className="absolute top-1/4 left-1/4 w-96 h-96 bg-primary/20 rounded-full blur-3xl opacity-60"></div>
          <div className="absolute bottom-1/4 right-1/4 w-96 h-96 bg-accent/20 rounded-full blur-3xl opacity-60"></div>
        </div>

        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 relative z-10 text-center py-16">
          <div className="inline-block mb-6">
            <span className="text-sm font-semibold text-accent bg-accent/10 px-4 py-2 rounded-full border border-accent/30 flex items-center gap-2 w-fit">
              <MessageCircle size={16} /> Get in Touch
            </span>
          </div>
          <h1 className="text-5xl lg:text-7xl font-bold mb-6">
            Let's Create <span className="gradient-text">Together</span>
          </h1>
          <p className="text-lg text-foreground/70 max-w-2xl mx-auto">
            Have questions? Need a consultation? Our AI assistant and team are here to help. Contact us for inquiries,
            bookings, or collaboration opportunities.
          </p>
        </div>
      </section>

      {/* Contact Info Cards */}
      <section className="py-20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-8 mb-20">
            <Card className="bg-card border-border p-8 text-center hover:border-accent/50 transition-all holographic-card group">
              <div className="w-14 h-14 rounded-lg bg-gradient-to-br from-primary/30 to-accent/30 flex items-center justify-center mx-auto mb-4 group-hover:shadow-lg group-hover:shadow-primary/50 transition-all">
                <Mail className="text-accent" size={28} />
              </div>
              <h3 className="text-xl font-semibold mb-2">Email</h3>
              <p className="text-foreground/70 mb-4">hello@melodymaker.com</p>
              <p className="text-sm text-foreground/50">We'll respond within 24 hours</p>
            </Card>

            <Card className="bg-card border-border p-8 text-center hover:border-accent/50 transition-all holographic-card group">
              <div className="w-14 h-14 rounded-lg bg-gradient-to-br from-primary/30 to-accent/30 flex items-center justify-center mx-auto mb-4 group-hover:shadow-lg group-hover:shadow-accent/50 transition-all">
                <Phone className="text-accent" size={28} />
              </div>
              <h3 className="text-xl font-semibold mb-2">Phone</h3>
              <p className="text-foreground/70 mb-4">+1 (310) 555-0123</p>
              <p className="text-sm text-foreground/50">Mon-Fri, 9am-6pm PST</p>
            </Card>

            <Card className="bg-card border-border p-8 text-center hover:border-accent/50 transition-all holographic-card group">
              <div className="w-14 h-14 rounded-lg bg-gradient-to-br from-primary/30 to-accent/30 flex items-center justify-center mx-auto mb-4 group-hover:shadow-lg group-hover:shadow-primary/50 transition-all">
                <MapPin className="text-accent" size={28} />
              </div>
              <h3 className="text-xl font-semibold mb-2">Location</h3>
              <p className="text-foreground/70 mb-4">Los Angeles, California</p>
              <p className="text-sm text-foreground/50">Serving worldwide</p>
            </Card>
          </div>
        </div>
      </section>

      {/* Contact Form Section */}
      <section className="py-20 bg-gradient-to-b from-card/20 to-background">
        <div className="max-w-3xl mx-auto px-4 sm:px-6 lg:px-8">
          <Card className="bg-card border-border p-8 lg:p-12 holographic-card">
            <h2 className="text-3xl font-bold mb-2 flex items-center gap-2">
              <Sparkles size={28} className="text-accent" />
              Send us a Message
            </h2>
            <p className="text-foreground/70 mb-8">
              Fill out the form below and we'll get back to you as soon as possible.
            </p>

            {submitted && (
              <div className="mb-8 p-4 rounded-lg bg-primary/10 border border-primary/50 text-primary">
                <p className="font-semibold">Thank you for your message!</p>
                <p className="text-sm">We'll be in touch soon. Check your email for confirmation!</p>
              </div>
            )}

            <form onSubmit={handleSubmit} className="space-y-6">
              {/* Name and Email Row */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div>
                  <label className="block text-sm font-medium mb-2">Name *</label>
                  <input
                    type="text"
                    name="name"
                    value={formData.name}
                    onChange={handleChange}
                    required
                    placeholder="Your name"
                    className="w-full px-4 py-3 rounded-lg bg-input border border-border text-foreground placeholder:text-foreground/40 focus:outline-none focus:border-accent focus:ring-2 focus:ring-accent/30 transition-all"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium mb-2">Email *</label>
                  <input
                    type="email"
                    name="email"
                    value={formData.email}
                    onChange={handleChange}
                    required
                    placeholder="your@email.com"
                    className="w-full px-4 py-3 rounded-lg bg-input border border-border text-foreground placeholder:text-foreground/40 focus:outline-none focus:border-accent focus:ring-2 focus:ring-accent/30 transition-all"
                  />
                </div>
              </div>

              {/* Phone and Subject Row */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div>
                  <label className="block text-sm font-medium mb-2">Phone</label>
                  <input
                    type="tel"
                    name="phone"
                    value={formData.phone}
                    onChange={handleChange}
                    placeholder="Your phone number"
                    className="w-full px-4 py-3 rounded-lg bg-input border border-border text-foreground placeholder:text-foreground/40 focus:outline-none focus:border-accent focus:ring-2 focus:ring-accent/30 transition-all"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium mb-2">Subject *</label>
                  <select
                    name="subject"
                    value={formData.subject}
                    onChange={handleChange}
                    required
                    className="w-full px-4 py-3 rounded-lg bg-input border border-border text-foreground focus:outline-none focus:border-accent focus:ring-2 focus:ring-accent/30 transition-all cursor-pointer"
                  >
                    <option value="">Select a subject</option>
                    <option value="booking">Project Booking</option>
                    <option value="collaboration">Artist Collaboration</option>
                    <option value="personalized">Personalized Song Request</option>
                    <option value="label">Join Our Label</option>
                    <option value="inquiry">General Inquiry</option>
                    <option value="support">Technical Support</option>
                    <option value="other">Other</option>
                  </select>
                </div>
              </div>

              {/* Message */}
              <div>
                <label className="block text-sm font-medium mb-2">Message *</label>
                <textarea
                  name="message"
                  value={formData.message}
                  onChange={handleChange}
                  required
                  placeholder="Tell us about your project..."
                  rows={6}
                  className="w-full px-4 py-3 rounded-lg bg-input border border-border text-foreground placeholder:text-foreground/40 focus:outline-none focus:border-accent focus:ring-2 focus:ring-accent/30 transition-all resize-none"
                ></textarea>
              </div>

              {/* Submit Button */}
              <Button
                type="submit"
                className="w-full bg-gradient-to-r from-primary to-accent hover:shadow-lg hover:shadow-primary/50 text-primary-foreground px-6 py-3 h-auto flex items-center justify-center gap-2 font-semibold"
              >
                <Send size={20} />
                Send Message
              </Button>
            </form>
          </Card>
        </div>
      </section>

      {/* FAQ Section */}
      <section className="py-20">
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
          <h2 className="text-4xl lg:text-5xl font-bold mb-16 text-center">Frequently Asked Questions</h2>

          <div className="space-y-6">
            {[
              {
                q: "What is your typical turnaround time for projects?",
                a: "Our standard turnaround is 2-4 weeks depending on the project scope. We also offer expedited services for rush projects with AI-powered acceleration.",
              },
              {
                q: "Do you offer remote collaboration?",
                a: "Yes! We have a fully equipped virtual studio with AI collaboration tools and can work with artists across the globe. Remote sessions are as professional as in-studio sessions.",
              },
              {
                q: "What genres do you specialize in?",
                a: "We work across all genres - pop, hip-hop, electronic, rock, jazz, R&B, indie, and more. Our AI-powered insights help us create in any style.",
              },
              {
                q: "Can I get revisions to my project?",
                a: "All our packages include unlimited revision rounds until you're completely satisfied. Our AI quality checks ensure consistency throughout.",
              },
              {
                q: "Do you offer payment plans?",
                a: "Yes, we offer flexible payment options for all services. Contact us to discuss a payment plan that works for your budget.",
              },
              {
                q: "How do I get started with a personalized song?",
                a: "Simply visit our Services page, select 'Request Personalised Song', and follow the process. Our team will guide you through every step!",
              },
            ].map((faq, idx) => (
              <Card
                key={idx}
                className="bg-card border-border p-6 hover:border-accent/50 transition-all holographic-card"
              >
                <h3 className="text-lg font-semibold mb-3 text-accent">{faq.q}</h3>
                <p className="text-foreground/70">{faq.a}</p>
              </Card>
            ))}
          </div>
        </div>
      </section>

      <div className="fixed bottom-6 right-6 z-50">
        {!chatOpen ? (
          <button
            onClick={() => setChatOpen(true)}
            className="w-14 h-14 rounded-full bg-gradient-to-r from-primary to-accent hover:shadow-lg hover:shadow-primary/50 flex items-center justify-center transition-all transform hover:scale-110 text-white"
          >
            <MessageCircle size={24} />
          </button>
        ) : (
          <Card className="w-96 h-[600px] flex flex-col bg-card border-border holographic-card shadow-2xl">
            {/* Chat Header */}
            <div className="bg-gradient-to-r from-primary to-accent text-primary-foreground p-4 rounded-t-lg flex items-center justify-between">
              <div className="flex items-center gap-2">
                <Zap size={20} />
                <h3 className="font-semibold">MelodyMaker AI Assistant</h3>
              </div>
              <button onClick={() => setChatOpen(false)} className="hover:bg-white/20 p-1 rounded transition-colors">
                <X size={20} />
              </button>
            </div>

            {/* Chat Messages */}
            <div className="flex-1 overflow-y-auto p-4 space-y-4 bg-background">
              {chatMessages.map((msg) => (
                <div key={msg.id} className={`flex ${msg.sender === "user" ? "justify-end" : "justify-start"}`}>
                  <div
                    className={`max-w-xs px-4 py-2 rounded-lg ${msg.sender === "user"
                      ? "bg-gradient-to-r from-primary to-accent text-primary-foreground"
                      : "bg-card border border-border text-foreground"
                      }`}
                  >
                    <p className="text-sm">{msg.text}</p>
                  </div>
                </div>
              ))}
            </div>

            {/* Chat Input */}
            <form onSubmit={handleChatSubmit} className="p-4 border-t border-border flex gap-2">
              <input
                type="text"
                value={chatInput}
                onChange={(e) => setChatInput(e.target.value)}
                placeholder="Ask me anything..."
                className="flex-1 px-3 py-2 rounded-lg bg-input border border-border text-foreground placeholder:text-foreground/40 focus:outline-none focus:border-accent text-sm transition-all"
              />
              <button
                type="submit"
                className="px-3 py-2 bg-gradient-to-r from-primary to-accent hover:shadow-lg text-white rounded-lg transition-all"
              >
                <Send size={18} />
              </button>
            </form>
          </Card>
        )}
      </div>
    </main>
  )
}
